.inputRegistration{
  width: 95px; 
  height: 30px;
  border-radius: 5px; 
  font-style: italic; 
  font-weight: bold;
  font-size: 14px;
}
.placeHolderForEdit{
  width: 95px; 
  height: 30px;
  border-radius: 5px; 
  font-style: italic; 
  font-weight: bold;
  font-size: 14px;
  opacity: 1;
}
.placeHolderForEditForAntDesigne{
  font-style: italic; 
  font-weight: bold;
  font-size: 14px;
  opacity: 1;
}

.placeHolderForEditForAntDesigneCalendar{
  font-style: italic; 
  font-weight: bold;
  font-size: 14px;
  opacity: 1;
}

.placeHolderForEditForAntDesigne:hover{
  border-color: #1990FF;
}


.fontModal{
    font-family: 'Pangolin', cursive;
    font-size: 16px;
}


.icons:hover {
    box-shadow: 0 0 10px 0 blue inset, 0 0 10px 4px blue;
    color: red;
}



.imageMainStringSearch {
    position	: relative;
    text-align: center;

}



