@media (max-width: 450px) and (min-width: 0px){
    .contentAboutUs{
        height: auto;
        /* background-color: grey; */
        font-size: 16px;
        opacity: 0.5;
        padding: "21px"; 
        text-align: "justify"
    }

    .advantages{
        flex-flow: row wrap;
    }

    .ibmxFontSize{
        font-size: small;
    }
}

@media (max-width: 700px) and (min-width: 451px){
    .contentAboutUs{
        height: auto;
        /* background-color: grey; */
        font-size: 25px;
        opacity: 0.5;
        padding: "21px"; 
        text-align: "justify"
    }

    .advantages1{
        margin: 0 auto;  
        justify-content: space-around;
       width: 30%;
       font-family: 'Caveat', cursive, 

    }

    .ibmxFontSize{
        font-size: small;
    }
}

@media (max-width: 2500px) and (min-width: 701px){
    .contentAboutUs{
        height: auto;
        /* background-color: grey; */
        font-size: 25px;
        opacity: 0.8;
        padding: "21px"; 
        text-align: "justify"
    }

    .advantages1{
        margin: 0 auto;  
        justify-content: space-around;
       width: 30%;
       font-family: 'Oswald', sans-serif;
     
    }

    .hoverCard:hover {
        -webkit-transform: scale(1.1);
                transform: scale(1.1);
        cursor: pointer;
    }

    .hoverCardEdit:hover {
        -webkit-transform: scale(1.01);
                transform: scale(1.01);
        cursor: pointer;
    }

    .ibmxFontSize{
        font-size: 21px;
    }

}


